import { c as create_ssr_component } from "../../../../chunks/ssr.js";
import { r as renderTextWithMathMode } from "../../../../chunks/KaTeX.js";
const css = {
  code: "div.svelte-5kpqjg{padding-bottom:80px;padding-left:70px}img.svelte-5kpqjg{height:400px}p.svelte-5kpqjg{font-size:20px}",
  map: null
};
const Page = create_ssr_component(($$result, $$props, $$bindings, slots) => {
  $$result.css.add(css);
  return `<h1 data-svelte-h="svelte-widt7r">Missing Thermal Concepts and Greenhouse Gas and Entropy</h1> <div class="svelte-5kpqjg"><h1 data-svelte-h="svelte-tinm1w">Kinetic Theory</h1> <p class="svelte-5kpqjg" data-svelte-h="svelte-p7sra5">Kinetic theory states that at a microscopic level all matter is made from particles. They are always in motion and attracted to each other through the IMF. Because they are in motion they have kinetic energy, and because they have attractive forces they have potential energy.</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-bw2awn">Adding or removing energy can either change the distance between the particles (PE) or the speed of the particles (KE). The internal energy is the sum of the kinetic energy and potential energy</p> <h2><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$u = KE + PE$$`)}<!-- HTML_TAG_END --></h2> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`Kelvin: The SI unit of temperature. $$0 C  = 273.15 K$$, $$\\Delta C = \\Delta k$$`)}<!-- HTML_TAG_END --></p> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`Density is a substance’s mass per unit volume. $$P = \\frac{m}{v}$$ in either $$kg m^{-3}$$ or $$g cm ^{-3}$$`)}<!-- HTML_TAG_END --></p></div> <div class="svelte-5kpqjg"><h1 data-svelte-h="svelte-rze4ga">Temperature Time Graphs</h1> <div class="svelte-5kpqjg" data-svelte-h="svelte-1jf55jt"><img src="https://cdn.discordapp.com/attachments/744944331542233173/1302775949225361439/image.png?ex=67295826&is=672806a6&hm=5850105e04c9c89c76a7184f3982f84ea8a04e418196581923df0c812d74ce6d&" class="svelte-5kpqjg"></div> <div class="svelte-5kpqjg" data-svelte-h="svelte-zyj6io"><img src="https://cdn.discordapp.com/attachments/744944331542233173/1302776447340908584/image.png?ex=6729589d&is=6728071d&hm=fc0ffed24bc9f47e56d0dfc632ef593edcd702fda93da1155b709443c6820504&" class="svelte-5kpqjg"></div> <p class="svelte-5kpqjg" data-svelte-h="svelte-h3mz11">The moment where the graph is rising is an increase of kinetic energy with the temperature of the object. Given this is a closed container this will increase linearly. When the graph is in a straight line is when the potential energy is increasing in the form of a phase shift.</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-12eeq3j">A graph of the temperature over a function of time is called a heating curve and will be around the same for every substance</p> <h2 data-svelte-h="svelte-1l8cl70">Specific Heat Capacity</h2> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`The specific heat capacity of a substance is the amount of energy required to heat 1 $$kg$$ of a substance by 1 degree celsius or kelvin (remember change in celsius is the same as change in kelvin).`)}<!-- HTML_TAG_END --></p> <h3><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$c = \\frac{Q}{m \\Delta T}$$`)}<!-- HTML_TAG_END --></h3> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`Units of $$J kg^{-1} K^{-1}$$`)}<!-- HTML_TAG_END --></p> <p class="svelte-5kpqjg" data-svelte-h="svelte-8e425r">Q is thermal energy. M is the mass of the object. C is the specific heat capacity. Delta T is the change in temperature (k or c).</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-136f643">The phase a substance is in changes its specific heat capacity</p> <h2 data-svelte-h="svelte-63whae">Specific Latent Heat</h2> <p class="svelte-5kpqjg" data-svelte-h="svelte-1lcmkt3">The specific latent heats of fusion and vaporization are the energy needed to change the phase of 1 kg of a substance at its melting (fusion) or boiling point (vaporization).</p> <h3><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$L = \\frac{Q}{m}$$`)}<!-- HTML_TAG_END --></h3> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$L_f$$ or $$L_v$$`)}<!-- HTML_TAG_END --></p> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`Units of $$J kg^{-1}$$`)}<!-- HTML_TAG_END --></p> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`Q is thermal energy. M is mass. $$L_f$$ is specific latent heat of fusion while $$L_v$$ is specific latent heat of vaporization`)}<!-- HTML_TAG_END --></p> <p class="svelte-5kpqjg" data-svelte-h="svelte-1psn13u">Note that fusion is the same as melting, and vaporization is the same as liquidating from a gaseous state</p> <img src="https://cdn.discordapp.com/attachments/744944331542233173/1302778655176527923/image.png?ex=67295aab&is=6728092b&hm=257bb772d6b96de250d839b0d58febf002e70ac84553a915d50178b7c4c0e451&" class="svelte-5kpqjg"> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`The gradients/slopes at the graph where kinetic energy is increasing is quantified by the formula $$Q=m c \\Delta t$$. The gradient of the line is the change in temperature over time, and objects with a higher specific heat capacity will need more energy to heat up, in turn decreasing the gradient.`)}<!-- HTML_TAG_END --></p> <img src="https://cdn.discordapp.com/attachments/744944331542233173/1302779057238311002/image.png?ex=67295b0b&is=6728098b&hm=6a3b8203e9df4eac36ad88c4bbf97e0805e8fb05f103eb69d2e44fa2c8b3d559&" class="svelte-5kpqjg"> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`When the object is going through a phase change the temperature of the line is staying the same, and the gradient is 0. $$Q = mL$$ applies here. The form with the highest specific latent heat will have a longer line. Keep in mind the specific latent heat of vaporization is always higher than the specific latent heat of fusion.`)}<!-- HTML_TAG_END --></p></div> <div class="svelte-5kpqjg"><p class="svelte-5kpqjg" data-svelte-h="svelte-81kci0">Two objects that are touching each other will transfer energy until they are in thermal equilibrium (at the same temperature). Due to the conservation of energy, the thermal energy lost by the otter one will be the same as the thermal energy gained by the colder one.</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-11x9j1c">For questions that look like that, use the formula</p> <h3><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$Q_{out} = Q_{in} $$`)}<!-- HTML_TAG_END --></h3> <h3><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$ -m_1 c_w \\Delta T = m_2 c_w \\Delta T $$`)}<!-- HTML_TAG_END --></h3> <p class="svelte-5kpqjg" data-svelte-h="svelte-6pd2vg">It is negative because the one losing temperature has a negative thermal energy</p></div> <div class="svelte-5kpqjg"><h1 data-svelte-h="svelte-1e0x2oa">Gas Laws</h1> <p class="svelte-5kpqjg" data-svelte-h="svelte-qo8ve8">Gasses can be described by their pressure, temperature, and volume. The temperature of a gas is directly proportional to its average kinetic energy. Pressure of a gas is the force of a gas against a container&#39;s walls. The volume of a gas is the space occupied by a gas. Because of the collisions between particles, this is the volume of the container it is in.</p> <h2 data-svelte-h="svelte-tt3nkb">Ideal Gasses</h2> <p class="svelte-5kpqjg" data-svelte-h="svelte-1ujmc86">Particles are identical</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-l221ax">Particles spacing are much larger than the particles themselves</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-1sdbx4t">No intermolecular forces</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-1aau0hr">Elastic collision</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-t9x6b3">In other words, if they have a high temperature, low pressure, and low density they can be considered ideal in this scope</p> <h2 data-svelte-h="svelte-1yy6f26">Moles</h2> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`1 mole is $$6.02 * 10^{23}$$ particles`)}<!-- HTML_TAG_END --></p> <p class="svelte-5kpqjg" data-svelte-h="svelte-ntyywr">The molar mass is the mass of one mole of a substance</p> <h2 data-svelte-h="svelte-17t9oul">Pressure</h2> <h4><!-- HTML_TAG_START -->${renderTextWithMathMode(`Pressure = $$F/A$$`)}<!-- HTML_TAG_END --></h4> <h4><!-- HTML_TAG_START -->${renderTextWithMathMode(`Pascal = $$N m^{-2}$$`)}<!-- HTML_TAG_END --></h4> <h4><!-- HTML_TAG_START -->${renderTextWithMathMode(`Pressure = $$\\frac{1}{3} \\rho \\overline{v}^2$$`)}<!-- HTML_TAG_END --></h4> <p class="svelte-5kpqjg" data-svelte-h="svelte-13oglu5">Because the IMF of ideal gasses is negligible, all ideal gasses have a PE of 0. This means that the internal energy is just the kinetic energy.</p> <h3><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$\\overline{E}_k = \\frac{3}{2} k_b T$$`)}<!-- HTML_TAG_END --></h3> <p class="svelte-5kpqjg" data-svelte-h="svelte-w7kwae">Kinetic energy average = 3/2 Boltzmann&#39;s constant times temperature</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-tb89u1">Therefor, the internal energy (total kinetic energy) can be determined by the average kinetic energy multiplied by the number of particles</p> <h3><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$u = \\frac{3}{2} k_b T N$$`)}<!-- HTML_TAG_END --></h3> <p class="svelte-5kpqjg" data-svelte-h="svelte-1gdotmu">Internal Energy = 3/2 Boltzmann&#39;s constant times temperature times number of particles</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-uqkv79">You can also represent this using a different formula using the number of moles</p> <h3><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$u = \\frac{3}{2} R T n$$`)}<!-- HTML_TAG_END --></h3> <p class="svelte-5kpqjg" data-svelte-h="svelte-cs4md9">Internal Energy = 3/2 gas constant times temperature times number of moles</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-xi02jt">Keep in mind that</p> <h3><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$K_b = \\frac{R}{N_a}$$`)}<!-- HTML_TAG_END --></h3></div> <div class="svelte-5kpqjg"><h1 data-svelte-h="svelte-1e0x2oa">Gas Laws</h1> <div class="svelte-5kpqjg"><h2 data-svelte-h="svelte-madewa">Boyle’s Law (Isothermal)</h2> <p class="svelte-5kpqjg" data-svelte-h="svelte-p8rrqc">Same temperature</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-11yvehf">Pressure is proportional to 1/volume</p> <h3><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$P_1 V_1 = P_2 V_2$$`)}<!-- HTML_TAG_END --></h3></div> <div class="svelte-5kpqjg"><h2 data-svelte-h="svelte-zjv8jw">Charles&#39; Law (Isobaric)</h2> <p class="svelte-5kpqjg" data-svelte-h="svelte-9m6jp1">Same pressure</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-rq41s">Volume proportional to temperature</p> <h3><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$\\frac{V_1}{T_1} = \\frac{V_2}{T_2}$$`)}<!-- HTML_TAG_END --></h3> <p class="svelte-5kpqjg"></p></div> <div class="svelte-5kpqjg"><h2 data-svelte-h="svelte-1edl7q0">Gay-Lussac’s Law (Isochoric\\isovolumetric)</h2> <p class="svelte-5kpqjg" data-svelte-h="svelte-131yrfo">Same volume</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-1gzmokg">The pressure is proportional to temperature</p> <h3><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$\\frac{P_1}{T_1} = \\frac{P_2}{T_2}$$`)}<!-- HTML_TAG_END --></h3></div> <div class="svelte-5kpqjg"><h2 data-svelte-h="svelte-cx2b5k">Ideal Gas Equation</h2> <h3><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$PV = nRT$$`)}<!-- HTML_TAG_END --></h3> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`P = pressure in Pascals/$$Pa$$/$$nm^{-2}$$`)}<!-- HTML_TAG_END --></p> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`V = volume in $$m^3$$`)}<!-- HTML_TAG_END --></p> <p class="svelte-5kpqjg" data-svelte-h="svelte-1uwkljv">n = number of moles</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-6bezag">R = gas constant</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-1lmz2b9">T = temperature in kelvin</p></div></div> <div class="svelte-5kpqjg"><h1 data-svelte-h="svelte-1olwncy">First Law of Thermodynamics</h1> <h2><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$Q = \\Delta U + w$$`)}<!-- HTML_TAG_END --></h2> <p class="svelte-5kpqjg" data-svelte-h="svelte-1uq43oy">Q is thermal energy. Delta U is the change in internal energy. W is the work done by the gas.</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-1ob2wft">If thermal energy enters a system, Q is set as a positive value, while leaving makes Q a negative value.</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-1553xrl">The increase in temperature leads to a positive delta U while a decrease in temperature leads to a negative delta U</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-18rndei">If the volume expands the work is positive, and if it decreases the work is negative. It is represented by the volume under a pressure volume gas.</p></div> <div class="svelte-5kpqjg"><h1 data-svelte-h="svelte-1ats2rw">Pressure Volume Graphs</h1> <div class="svelte-5kpqjg" data-svelte-h="svelte-16judxf"><h2>Isochoric</h2> <p class="svelte-5kpqjg">Volume is constant</p> <p class="svelte-5kpqjg">Gay-Lussac&#39;s Law</p> <p class="svelte-5kpqjg">Vertical line on volume pressure graph</p> <p class="svelte-5kpqjg">No work done</p> <img src="https://cdn.discordapp.com/attachments/744944331542233173/1302806190698397706/image.png?ex=67297450&is=672822d0&hm=11fa5b73758b52effdb24d9089aea9c148c2b2485e0a328171e7b393356f88a6&" class="svelte-5kpqjg"></div> <div class="svelte-5kpqjg" data-svelte-h="svelte-1ka8nfq"><h2>Isothermal</h2> <p class="svelte-5kpqjg">Constant temperature</p> <p class="svelte-5kpqjg">Boyle’s law</p> <p class="svelte-5kpqjg">Hotter isothermal processes create a line up and to the right of cooler ones</p> <img src="https://cdn.discordapp.com/attachments/744944331542233173/1302807174195708016/image.png?ex=6729753b&is=672823bb&hm=28f20e82f3d18c7e6434f95fa0001c57cf34e2a075180762efe2da415cdcdf36&" class="svelte-5kpqjg"></div> <div class="svelte-5kpqjg"><h2 data-svelte-h="svelte-100wllc">Atiabatic</h2> <p class="svelte-5kpqjg" data-svelte-h="svelte-w1dkhw">No thermal energy in or out</p> <p class="svelte-5kpqjg" data-svelte-h="svelte-1iduuwh">Not possible in real life</p> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$Q = 0$$`)}<!-- HTML_TAG_END --></p> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$PV^{\\frac{5}{3}} = constant$$`)}<!-- HTML_TAG_END --></p> <p class="svelte-5kpqjg"><!-- HTML_TAG_START -->${renderTextWithMathMode(`$$\\frac{P_1V_1}{T_1} = \\frac{P_2V_2}{T_2}$$`)}<!-- HTML_TAG_END --></p> <p class="svelte-5kpqjg" data-svelte-h="svelte-dmw87j">Adiabatic curves are much steeper than isothermal ones</p> <img src="https://cdn.discordapp.com/attachments/744944331542233173/1302807772886470738/image.png?ex=672975ca&is=6728244a&hm=a87b9e5392e179e768e8c8f098eeea939e4f53aa3fb327c91e9ad14773b070b3&" class="svelte-5kpqjg"></div> </div>`;
});
export {
  Page as default
};
